import React from 'react';
  

import { Bar } from 'react-chartjs-2';

interface MessageTypeDistributionProps {
  title: string;
}

const MessageTypeDistribution: React.FC<MessageTypeDistributionProps> = ({ title }) => {
  const data = {
    labels: ['MT103', 'MT202', 'MT700', 'MT760'],
    datasets: [
      {
        label: 'Messages',
        data: [450, 320, 280, 190],
        backgroundColor: ['#36A2EB', '#A569BD', '#FF6384', '#FFCE56'],
      },
    ],
  };

  const options = {
    indexAxis: 'y' as const,
    responsive: true,
    maintainAspectRatio: false,
  };

  return (
    <div className="chart-component">
      <div className="chart-header">
        <h3>{title}</h3>
      </div>
      <div className="message-type-chart-container">
        <Bar data={data} options={options} />
      </div>
    </div>
  );
};

export default MessageTypeDistribution;