import React from 'react';
import ChartComponent from './ChartComponent';
import SummaryComponent from './SummaryComponent';
import MessageTypeDistribution from './MessageTypeDistribution';

const OverviewPage: React.FC = () => {
  return (
    <div className="overview-page">
      <div className="dashboard-row">
        <div className="dashboard-column completion">
          <ChartComponent title="Messages by Completion" type="pie" />
        </div>
        <div className="dashboard-column trend">
          <ChartComponent title="Weekly Trend" type="bar" />
        </div>
      </div>
      
      <div className="dashboard-row">
        <div className="dashboard-column">
        <MessageTypeDistribution title="Message Type Distribution" />
        </div>
        <div className="dashboard-column">
          <SummaryComponent title="Recent Activity" />
        </div>
        <div className="dashboard-column">
          <SummaryComponent title="Message Trend Summary" />
        </div>
      </div>
    </div>
  );
};

export default OverviewPage;